@import url(https://fonts.googleapis.com/css2?family=Baloo+Bhaijaan+2:wght@400;500&display=swap);
body {
  font-family: "Baloo Bhaijaan 2", cursive;
}
.s12 {
  width: 90%;
  position: relative;
  margin-left: auto;
  margin-right: auto;
  min-width: 12rem;
  overflow: hidden;
  margin-top: 1rem;
  border-radius: 10px;
  cursor: pointer;
  background-color: antiquewhite;
  transition: background-color 1s ease;
}

.s12:hover {
  background-color: aquamarine;
}
#main_text {
  width: 50%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 3rem;
  font-size: large;
}
#main_text p{
  border-top: 1px solid;
  border-bottom: 1px solid;
  font-size: medium;
}
#bttnBlock{
  position: absolute;
  
}
textarea
{
  width:100% !important;
  height: -webkit-max-content !important;
  height: max-content !important;
}

.btn-large{
  margin-left: 1rem;
  border-radius: 20px;
  width: 5rem;
  height: 3rem;
  background-color:#ee6e73;
  transition: background-color 1s ease;
  cursor: pointer;
}
.bttnBlock_bttn:hover{
  background-color: rgb(233, 201, 19);
}

.similar_card{
  width: 30%;
  float: left;
}

.red{
  margin-top: 1rem;
  border-radius: 50% !important;
  position: absolute;
}
#create_{
  width: -webkit-max-content;
  width: max-content;
}

#similar{
  width: 65%;
display: flex;
margin-left: auto;
margin-right: auto;
justify-content: space-between;
}
